<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
        <span class="inner"></span>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-add" @click.stop.prevent="addCart"></div>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: ["food"],
  methods: {
    addCart(event) {
      if (!this.food.count) {
        Vue.set(this.food, "count", 1);
      } else {
        this.food.count++;
      }

      this.$emit("add", event.target);
    },
    decreaseCart() {
      this.food.count--;
    }
  }
};
</script>

<style lang="less">
.cartcontrol {
  font-size: 0;
  
  .cart-decrease {
    display: inline-block;
    padding: 5px;
    opacity: 1;
    transform: translate3d(0, 0, 0);
      &.move-enter-active,
      &.move-leave-active {
        transition: all 0.4s linear;
        
      }
      &.move-enter,
      &.move-leave-active {
        opacity: 0;
        transform: translate3d(24px, 0, 0);
        transform: rotate(180deg);
      }
    .inner {
      display: inline-block;
      background-image: url("../../assets/SVG/remove_circle_outline.svg");
      width: 24px;
      height: 24px;
      line-height: 24px;
      background-size: cover;
      transition: all 0.4s linear;
      transform: rotate(0);
    
    }
  }
  .cart-count {
    display: inline-block;
    width: 12px;
    font-size: 10px;
    vertical-align: top;
    color: rgb(147, 153, 159);
    text-align: center;
    line-height: 24px;
    padding-top: 5px;
  }
  .cart-add {
    display: inline-block;
    // padding: 5px;
    background-image: url("../../assets/SVG/add_circle.svg");
    width: 24px;
    height: 24px;
    line-height: 24px;
    background-size: cover;
    font-size: 24px;
    margin-left: 5px;
    font-size: 24px;
  }
}
</style>