<template>
  <div>
    <div class="shopcard">
      <div class="content">
        <div class="content-left" @click="toggleList">
          <div class="logo-wrapper">
            <div class="logo" :class="{'highlight':totalCount>0}">
              <i class="icon"></i>
            </div>
            <div class="num">{{totalCount}}</div>
          </div>
          <div class="price" :class="{'highlight':totalPrice>0}">￥{{totalPrice}}</div>
          <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
        </div>
        <div class="content-right">
          <div class="pay" :class="payClass">{{payDesc}}</div>
        </div>
      </div>
      <div class="ball-container">
        <div v-for="(ball, index) in balls" :key="index">
          <transition
            name="drop"
            @before-enter="beforeDrop"
            @enter="dropping"
            @after-enter="afterDrop"
          >
            <div class="ball" v-show="ball.show">
              <div class="inner inner-hook"></div>
            </div>
          </transition>
        </div>
      </div>
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listcontent">
            <ul>
              <li v-for="(item, index) in Foods" :key="index" class="food">
                <span class="name">{{item.name}}</span>
                <div class="price">
                  <span>￥{{item.price*item.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="item" @add="addFood"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div class="mask" v-show="listShow" @click="hideList"></div>
    </transition>
  </div>
</template>

<script>
import cartcontrol from "../cartcontrol/cartcontrol";
import BScroll from "better-scroll";

export default {
  props: ["Foods", "deliveryPrice", "minPrice"],
  components: {
    cartcontrol
  },
  data() {
    return {
      balls: [
        { show: false },
        { show: false },
        { show: false },
        { show: false },
        { show: false }
      ],
      show: false,
      dropBalls: []
    };
  },
  computed: {
    totalCount() {
      let count = 0;
      this.Foods.forEach(food => {
        count += food.count;
      });
      return count;
    },
    totalPrice() {
      let total = 0;
      this.Foods.forEach(food => {
        total += food.price * food.count;
      });
      return total;
    },
    payDesc() {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}元起送`;
      } else if (this.totalPrice < this.minPrice) {
        let diff = this.minPrice - this.totalPrice;
        return `还差￥${diff}元起送`;
      } else {
        return "去结算";
      }
    },
    payClass() {
      if (this.totalPrice < this.minPrice) {
        return "no-enough";
      } else {
        return "enough";
      }
    },
    listShow: {
      // 控制购物车列表的显示和隐藏
      get() {
        if (!this.totalCount) {
          // 如果选择食品数量为 0 ，则不显示
          return false;
        }
        return this.show;
      },
      set() {
        if (!this.totalCount) {
          this.show = false;
        }
        if (!this.fold) {
          this.$nextTick(() => {
            // 创建 better-scroll 实例或者 refresh
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.listcontent, {
                click: true
              });
            } else {
              this.scroll.refresh();
            }
          });
        }
      }
    }
  },
  methods: {
    toggleList() {
      if (!this.totalCount) {
        return;
      }
      this.listShow = false; // 主要目的创建 better-scroll 实例或者 refresh
      this.show = !this.show; // 通过 this.fold 的修改，控制计算属性 lisShow，从而控制购物车列表的显隐
      // console.log("点击");
    },
    empty() {
      this.Foods.forEach(food => {
        food.count = 0;
      });
      this.listShow = false; // 隐藏购物车列表
    },
    hideList() {
      this.show = false;
    },
    addFood(target) {
      console.log("添加小球");

      this.drop(target);
    },
    drop(el) {
      // console.log("el", el);

      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          //将cartControl传过来的触发对象挂载到ball的el属性上
          ball.el = el;
          this.dropBalls.push(ball);
          return;
        }
      }
      // console.log(this.dropBalls);
    },
    beforeDrop(el) {
      // getBoundingClientRect用于获取某个元素相对于视窗的位置集合
      console.log(el);
      let count = this.balls.length;
      while (count--) {
        let ball = this.balls[count];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          console.log(rect);
          let x = rect.left - 32; //小球x方向位移= 小球距离屏幕左侧的距离-外层盒子距离水平的距离
          let y = -(window.innerHeight - rect.top - 22);// 负数，因为是从左上角向下
          el.style.display = ""; //设置初始位置前，手动置空，覆盖之前的display：none，使其显示
          el.style.webkitTransform = `translate3d(0,${y}px,0)`;
          el.style.transform = `translate3d(0,${y}px,0)`; // 设置外层盒子，即小球垂直方向的位移
          let inner = el.getElementsByClassName("inner-hook")[0]; //内层元素做横向动画,inner-hook（用于js选择的样式名加上-hook，表明只是用于js选择的，没有真实的样式含义）
          inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
          inner.style.transform = `translate3d(${x}px,0,0)`; // 设置内层盒子，即小球水平方向的距离
        }
      }
    },
    dropping(el, done) {
      // let rf = el.offsetHeight;
      console.log(done);

      this.$nextTick(() => {
        el.style.webkitTransform = "translate3d(0,0,0)";
        el.style.transform = "translate3d(0,0,0)";// 设置小球掉落后最终的位置
        let inner = el.getElementsByClassName("inner-hook")[0];
        inner.style.webkitTransform = "translate3d(0,0,0)";
        inner.style.transform = "translate3d(0,0,0)";
        el.addEventListener("transitionend", done);// Vue为了知道过渡的完成，必须设置相应的事件监听器。
      });
    },
    afterDrop(el) {
      let ball = this.dropBalls.shift(); // 完成一次动画就删除一个dropBalls的小球
      // console.log(ball);

      if (ball) {
        ball.show = false;
        el.style.display = "none";
      }
    }
  }
};
</script>

<style lang="less" scope>
.shopcard {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 50px;
  //   height: 300px;
  background: gold;
  .content {
    display: flex;
    background: #141d27;
    color: rgba(255, 255, 255, 0.4);
      font-size: 0;
    .content-left {
      flex: 1;
      .logo-wrapper {
        display: inline-block;
        vertical-align: top;
        position: relative;
        top: -10px;
        margin-left: 12px;
        padding: 5px;
        width: 56px;
        height: 56px;
        box-sizing: border-box;
        border-radius: 50%;
        background: #141d27;
        .logo {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: #2b343c;
          text-align: center;
          &.highlight {
            background: rgb(0, 160, 220);
          }
          .icon {
            display: inline-block;
            width: 50%;
            height: 50%;
            background-image: url("../../assets/SVG/shopping_cart.svg");
            background-size: cover;
            background-position: center;
            margin-top: 10px;
          }
        }
        .num {
          position: absolute;
          top: 0;
          right: 0;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          border-radius: 16px;
          font-size: 9px;
          font-weight: 700;
          color: #fff;
          background: rgb(240, 20, 20);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
        }
      }
      .price {
        display: inline-block;
        vertical-align: top;
        margin-top: 12px;
        font-weight: 700;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        box-sizing: border-box;
        padding-right: 12px;
        line-height: 24px;
           font-size: 16px;
        &.highlight {
          color: white;
        }
      }
      .desc {
        display: inline-block;
        vertical-align: top;
        line-height: 24px;
        font-size: 10px;
        margin-top: 12px;
        margin-left: 12px;
      }
    }
    .content-right {
      flex: 0 0 105px;
      .pay {
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-size: 12px;
        font-weight: 700;
        background: #2b333b;
        &.no-enough {
          background: #2b333b;
        }
        &.enough {
          background: #00b43c;
          color: #fff;
        }
      }
    }
  }
  .ball-container {
    .ball {
      position: fixed;
      left: 32px;
      bottom: 22px;
      z-index: 200;
      transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
      .inner {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: rgb(0, 160, 220);
        transition: all 0.4s linear;
      }
    }
  }
  .shopcart-list {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    transform: translate3d(0, -100%, 0);
    &.fold-enter-active,
    &.fold-leave-active {
      transition: all 0.5s;
    }

    &.fold-enter,
    &.fold-leave-active {
      transform: translate3d(0, 0, 0);
    }
    .list-header {
      height: 40px;
      line-height: 40px;
      padding: 0 20px;
      background: #f3f5f7;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      .title {
        float: left;
        font-size: 14px;
        margin: 0;
        padding: 0;
      }
      .empty {
        float: right;
        font-size: 12px;
        color: rgb(0, 160, 220);
      }
    }
    .list-content {
      padding: 0 20px;
      max-height: 218px;
      background: white;
      overflow: hidden;
      ul {
        margin: 0;
        padding: 0;
        .food {
          position: relative;
          padding: 12px 0;
          box-sizing: border-box;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          .name {
            font-size: 14px;
            line-height: 24px;
          }
          .price {
            position: absolute;
            right: 90px;
            bottom: 12px;
            font-size: 14px;
            line-height: 24px;
            font-weight: 700;
            color: rgb(240, 20, 20);
            vertical-align: middle;
          }
          .cartcontrol-wrapper {
            position: absolute;
            right: 0;
            bottom: 5px;
          }
        }
      }
    }
  }
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  backdrop-filter: blur(10px);
  opacity: 1;
  background: rgba(7, 17, 27, 0.6);
  &.fade-enter-active,
  &.fade-leave-active {
    transition: all 0.5s;
  }

  &.fade-enter,
  &.fade-leave-active {
    opacity: 0;
    background: rgba(7, 17, 27, 0);
  }
}
</style>