<template>
  <div>
    <div class="seller" ref="seller">
      <div class="seller-content">
        <div class="overview">
          <h1 class="title">{{seller.name}}</h1>
          <div class="desc">
            <star :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
          </div>
          <ul class="remark">
            <li class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="stress">{{seller.minPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}</span>分钟
              </div>
            </li>
          </ul>
          <div class="favorite" @click="toggleFavorite" :class="{'active':isFavorite}">
            <span class="icon"></span>
            <span class="text">{{favoriteText}}</span>
          </div>
        </div>
        <split></split>
        <div class="bulletin">
          <h1 class="title">公告与活动</h1>
          <div class="content-wrapper">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item" v-for="(item, index) in seller.supports" :key="index">
              <span class="icon" :class="classMap[item.type]"></span>
              <span class="text">{{item.description}}</span>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="pics">
          <div class="pic-wrapper">
            <h1 class="title">商家实景</h1>
            <div class="pic-wrapper" ref="picWrapper">
              <ul class="pic-list" ref="picList">
                <li class="pic-item" v-for="(pic, index) in seller.pics" :key="index">
                  <img :src="pic" alt width="120" height="90" />
                </li>
              </ul>
            </div>
          </div>
        </div>
        <split></split>
        <div class="info">
          <h1 class="title">商家信息</h1>
          <ul>
            <li class="info-item" v-for="(item, index) in seller.infos" :key="index">{{item}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import star from "../../components/star/star";
import split from "../../components/split/split";
import BScroll from "better-scroll";

export default {
  props: ["seller"],
  data() {
    return {
      classMap: ["aa", "bb", "cc", "dd", "ee"],
      isFavorite: () => {
        return window.localStorage.getItem("favorite") ? true : false;
      }
    };
  },
  components: {
    star,
    split
  },
  computed: {
    favoriteText() {
      return this.isFavorite ? "已收藏" : "收藏";
    }
  },
  methods: {
    toggleFavorite() {
      // console.log("点击");
      this.isFavorite = !this.isFavorite;
      if (this.isFavorite) {
        window.localStorage.setItem(
          "favorite",
          JSON.stringify({ data: this.seller.name })
        );
      } else {
        localStorage.removeItem("favorite");
      }
    },
    initScroll() {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
          click: true
        });
      } else {
        this.scroll.refresh();
        console.log("初始化拖拽");
      }
    },
    initPic() {
      if (this.seller.pics) {
        let picWidth = 120;
        let margin = 5;
        let picListWidth =
          (picWidth + margin) * this.seller.pics.length - margin;
        this.$refs.picList.style.width = picListWidth + "px";

        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              // 当设置为 true 的时候，可以开启横向滚动。
              scrollX: true,
              // 纵向的滚动保留原生滚动
              eventPassthrough: "vertical"
            });
          } else {
            this.picScroll.refresh();
          }
        });
      }
    }
  },
  watch: {
    seller() {
      this.$nextTick(() => {
        this.initScroll();
        this.initPic();
      });
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initScroll();
      this.initPic();
      console.log("挂载了");
    });
  }
};
</script>

<style lang="less" scope>
.seller {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  .overview {
    position: relative;
    padding: 18px;
    .title {
      margin-bottom: 10px;
      line-height: 14px;
      color: rgb(7, 17, 27);
      font-size: 16px;
    }
    .desc {
      padding-bottom: 20px;
      border-bottom: 1px solid (rgba(7, 17, 27, 0.1));
      .star {
        margin: 0;
        margin-right: 10px;
      }
      .text {
        display: inline-block;
        margin-right: 12px;
        vertical-align: top;
        font-size: 12px;
        color: rgb(77, 85, 93);
      }
    }
    ul.remark {
      margin: 0;
      padding: 0;
      display: flex;
      padding-top: 20px;
      .block {
        flex: 1;
        text-align: center;
        border-right: 1px solid rgba(7, 17, 27, 0.1);
        &:last-child {
          border: none;
        }
        h2 {
          font-size: 12px;
          line-height: 10px;
          color: rgb(147, 153, 159);
        }
        .content {
          line-height: 24px;
          color: rgb(7, 17, 27);
          font-size: 24px;
          font-weight: 100;
        }
      }
    }
    .favorite {
      position: absolute;
      width: 50px;
      right: 10px;
      top: 20px;
      text-align: center;
      &.active .text {
        color: rgb(240, 20, 20);
      }

      .icon {
        background-image: url("../../assets/SVG/favorite.svg");
        width: 25px;
        height: 25px;
        display: block;
        background-size: 25px 25px;
        margin-left: 13px;
      }
      .text {
        line-height: 10px;
        font-size: 10px;
        color: rgb(77, 85, 93);
      }
    }
  }
  .bulletin {
    padding: 20px 0;
    padding-left: 20px;
    .title {
      margin-bottom: 10px;
      font-size: 14px;
      color: rgb(7, 17, 27);
      line-height: 14px;
    }
    .content-wrapper {
      padding: 0 12px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      .content {
        font-size: 12px;
        color: rgb(240, 20, 20);
        line-height: 2em;
      }
    }
    .supports {
      margin: 0;
      padding: 0;
      .support-item {
        padding: 15px 12px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        &:last-child {
          border: none;
        }
        .icon {
          display: inline-block;
          width: 16px;
          height: 16px;
          background-size: 16px 16px;
          background-repeat: no-repeat;
          vertical-align: middle;
          &.aa {
            background-image: url("../../assets/img/decrease_4@2x.png");
          }
          &.bb {
            background-image: url("../../assets/img/discount_4@2x.png");
          }
          &.cc {
            background-image: url("../../assets/img/special_4@2x.png");
          }
          &.dd {
            background-image: url("../../assets/img/invoice_4@2x.png");
          }
          &.ee {
            background-image: url("../../assets/img/guarantee_4@2x.png");
          }
        }
        .text {
          font-size: 12px;
          color: rgb(7, 17, 27);
          margin-left: 5px;
        }
      }
    }
  }
  .pics {
    padding: 20px;
    .title {
      margin-bottom: 10px;
      color: rgb(7, 17, 27);
      font-size: 14px;
    }
    .pic-wrapper {
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      .pic-list {
        padding: 0;
        margin: 0;
        .pic-item {
          display: inline-block;
          margin-right: 5px;
          width: 120px;
          height: 90px;
          &:last-child {
            margin: 0;
          }
        }
      }
    }
  }

  .info {
    padding: 20px;
    .title {
      padding-bottom: 10px;
      font-size: 14px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    }
    ul {
      padding: 0;
      margin: 0;
      .info-item {
        padding: 15px 12px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        font-size: 12px;
        &:last-child {
          border: none;
        }
      }
    }
  }
}
</style>