<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <div class="score">{{seller.score}}</div>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <star :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect
        :ratings="ratings"
        :selectType="selectType"
        :onlyContent="onlyContent"
        @select="slectRating($event)"
        @toggle="toggleContent"
      ></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li
            v-for="(item, index) in ratings"
            :key="index"
            class="rating-item"
            v-show="needShow(item.rateType,item.text)"
          >
            <div class="avatar">
              <img :src="item.avatar" alt width="28" height="28" />
            </div>
            <div class="content">
              <h1 class="name">{{item.username}}</h1>
              <div class="star-wrapper">
                <star :score="item.score"></star>
                <span class="delivery" v-show="item.deliveryTime">{{item.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{item.text}}</p>
              <div class="time">{{calculateTime(item.rateTime)}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import star from "../../components/star/star";
import split from "../../components/split/split";
import ratingselect from "../../components/ratingselect/ratingselect";

export default {
  props: ["seller"],
  components: {
    star,
    split,
    ratingselect
  },
  data() {
    return {
      ratings: [],
      selectType: 2,
      onlyContent: true
    };
  },
  computed: {},
  methods: {
    getRatingMeg() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://ustbhuangyi.com/sell/api/ratings"
        )
        .then(response => {
          ({ data: this.ratings } = response.data);
        });
    },
    calculateTime(time) {
      var curtime = new Date(time);
      var month = curtime.getMonth();
      var day = curtime.getDay();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      return (
        curtime.getFullYear() +
        "-" +
        month +
        "-" +
        day +
        "-" +
        curtime.getHours() +
        ":" +
        curtime.getMinutes()
      );
    },
    slectRating(type) {
      this.selectType = type;
      // console.log(type);
    },
    needShow(type, text) {
      if (!text && this.onlyContent) {
        return false;
      }
      if (this.selectType === 2) {
        return true;
      } else {
        return type == this.selectType;
      }
    },
    toggleContent() {
      this.onlyContent = !this.onlyContent;
    }
  },
  created() {
    this.getRatingMeg();
  }
};
</script>

<style lang="less">
.ratings {
  position: absolute;
  top: 174px;
  left: 0;
  width: 100%;
  // overflow: hidden;
  .overview {
    display: flex;
    padding: 18px 0;
    .overview-left {
      flex: 0 0 137px;
      padding: 5px 0;
      border-right: 1px solid rgba(7, 17, 27, 0.1);
      text-align: center;
      .score {
        margin-bottom: 5px;
        line-height: 30px;
        font-size: 24px;
        color: rgb(255, 153, 0);
      }
      .title {
        margin-bottom: 8px;
        line-height: 12px;
        font-size: 12px;
        color: rgb(7, 17, 27);
      }
      .rank {
        line-height: 12px;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
    }
    .overview-right {
      flex: 1;
      padding: 5px 0 5px 25px;
      font-size: 12px;

      .score-wrapper {
        margin-bottom: 8px;
        .title {
          display: inline-block;
          line-height: 18px;
          color: rgb(7, 17, 27);
          display: inline-block;
        }

        .score {
          display: inline-block;
          line-height: 18px;
          vertical-align: top;
          color: rgb(255, 153, 0);
        }
      }
      .delivery-wrapper {
        .title {
          line-height: 18px;
          color: rgb(7, 17, 27);
        }
        .delivery {
          margin-left: 12px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }
  .rating-wrapper {
    padding: 0 18px;
    ul {
      padding: 0;
      margin: 0;
      .rating-item {
        display: flex;
        padding: 18px 0;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        .avatar {
          flex: 0 0 28px;
          margin-right: 10px;
          img {
            border-radius: 50%;
          }
        }
        .content {
          position: relative;
          flex: 1;
          .name {
            margin-bottom: 5px;
            line-height: 12px;
            font-size: 12px;
          }
          .star-wrapper {
            margin-bottom: 5px;
            .star {
              margin: 0 5px 0 0;
              vertical-align: top;
            }
            .delivery {
              line-height: 12px;
              font-size: 10px;
              color: rgb(147, 153, 159);
              vertical-align: middle;
            }
          }
          .text {
            margin-bottom: 8px;
            color: rgb(7, 17, 27);
            font-size: 12px;
          }
          .time {
            position: absolute;
            top: 8px;
            right: 0;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }
      }
    }
  }
}
</style>