<template>
  <transition name="move">
    <div v-show="showFlag" class="food" ref="food">
      <div class="foot-content">
        <div class="image-header">
          <img :src="selectedFood.image" />
          <div class="back" @click="showFlag=false">
            <i class="icon"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{selectedFood.name}}</h1>
          <div class="detail">
            <span class="sell-count">月售{{selectedFood.sellCount}}份</span>
            <span class="rating">好评率{{selectedFood.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{selectedFood.price}}</span>
            <span class="old" v-show="selectedFood.oldPrice">￥{{selectedFood.oldPrice}}</span>
          </div>
          <div class="cartcontrol-wrapper">
            <cartcontrol @add="addFood" :food="selectedFood"></cartcontrol>
          </div>
          <transition name="fade">
            <div class="buy" v-show="!selectedFood.count" @click.stop.prevent="addFirst">加入购物车</div>
          </transition>
        </div>
        <split v-show="selectedFood.info"></split>
        <div class="info" v-show="selectedFood.info">
          <h1 class="title">商品信息</h1>
          <p class="text">{{selectedFood.info}}</p>
        </div>
        <split></split>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <ratingselect
            :ratings="selectedFood.ratings"
            :selectType="selectType"
            :onlyContent="onlyContent"
            @select="slectRating($event)"
            @toggle="toggleContent"
          ></ratingselect>
          <div class="rating-wrapper">
            <ul v-show="selectedFood.ratings">
              <li
                v-show="needShow(item.rateType,item.text)"
                v-for="(item, index) in selectedFood.ratings"
                :key="index"
                class="rating-item"
              >
                <div class="user">
                  <span class="name">{{item.username}}</span>
                  <img :src="item.avatar" alt width="12" height="12" class="avatar" />
                </div>
                <div class="time">{{calculateTime(item.rateTime)}}</div>
                <p class="text">
                  <span :class="{'up':item.rateType==0,'down':item.rateType==1}"></span>
                  {{item.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-show="!selectedFood.ratings">暂无评论</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import BScroll from "better-scroll";
import split from "../../components/split/split";
import ratingselect from "../../components/ratingselect/ratingselect";
import cartcontrol from "../../components/cartcontrol/cartcontrol";
import Vue from "vue";
export default {
  props: ["selectedFood"],
  data() {
    return {
      showFlag: false,
      selectType: 2,
      onlyContent: true
    };
  },
  components: {
    split,
    ratingselect,
    cartcontrol
  },
  methods: {
    show() {
      this.showFlag = true;
      this.selectType = 2;
      this.onlyContent = true;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    calculateTime(time) {
      var curtime = new Date(time);
      var month = curtime.getMonth();
      var day = curtime.getDay();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        month = "0" + day;
      }
      return (
        curtime.getFullYear() +
        "-" +
        month +
        "-" +
        day +
        "-" +
        curtime.getHours() +
        ":" +
        curtime.getMinutes()
      );
    },
    slectRating(type) {
      this.selectType = type;
    },
    toggleContent() {
      this.onlyContent = !this.onlyContent;
    },
    needShow(type, text) {
      if (!text && this.onlyContent) {
        return false;
      }
      if (this.selectType == 2) {
        return true;
      } else {
        return type === this.selectType;
      }
    },
    addFood(target) {
      this.$emit("add", target);
    },
    addFirst(event) {
      this.$emit("add", event.target);
      Vue.set(this.selectedFood, "count", 1);
    }
  }
};
</script>

<style lang="less">
.food {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 30;
  width: 100%;
  bottom: 48px;
  transform: translate3d(0, 0, 0);
  background: white;
  &.move-enter-active,
  &.move-leave-active {
    transition: all 0.2s linear;
  }
  .move-enter,
  &.move-leave-active {
    transform: translate3d(100%, 0, 0);
  }

  .image-header {
    position: relative;
    width: 100%;
    padding-top: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    .back {
      position: absolute;
      top: 10px;
      left: 5px;
      .icon {
        background-image: url("../../assets/SVG/arrow_lift.svg");
        display: block;
        width: 25px;
        height: 25px;
        padding: 10px;
      }
    }
  }
  .content {
    position: relative;
    padding: 20px;
    .title {
      //   margin-bottom: 10px;
      font-size: 14px;
      font-weight: 700;
      line-height: 14px;
    }
    .detail {
      margin-bottom: 15px;
      font-size: 10px;
      color: rgb(147, 153, 159);
      .sell-count {
        margin-right: 12px;
      }
    }
    .price {
      font-weight: 700;
      line-height: 25px;
      .now {
        margin-right: 10px;
        font-size: 14px;
        color: red;
      }
      .old {
        text-decoration: line-through;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
    }
    .cartcontrol-wrapper {
      position: absolute;
      right: 12px;
      bottom: 12px;
    }
    .buy {
      position: absolute;
      right: 18px;
      bottom: 18px;
      z-index: 10;
      line-height: 14px;
      padding: 5px 12px;
      background: rgb(0, 160, 220);
      font-size: 10px;
      border-radius: 12px;
      color: white;
      opacity: 1;
      box-sizing: border-box;
      &.fade-enter-active,
      &.fade-leave-active {
        transition: all 0.2s;
      }

      &.fade-enter,
      &.fade-leave-active {
        opacity: 0;
        z-index: -1;
      }
    }
  }
  .info {
    padding: 20px;
    .title {
      line-height: 14px;
      margin-bottom: 5px;
      font-size: 14px;
    }
    .text {
      padding: 0 8px;
      font-size: 12px;
      line-height: 24px;
    }
  }
  .rating {
    padding-top: 20px;
    .title {
      margin-left: 20px;
      font-size: 14px;
      line-height: 14px;
    }
    .rating-wrapper {
      padding: 0 20px;
      ul {
        padding: 0;
        margin: 0;
        .rating-item {
          position: relative;
          padding: 15px 0;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          .user {
            position: absolute;
            right: 0;
            top: 15px;
            .name {
              display: inline-block;
              margin-right: 5px;
              font-size: 10px;
              color: rgb(147, 153, 159);
              vertical-align: middle;
            }
            .avatar {
              border-radius: 50%;
            }
          }
          .time {
            margin-bottom: 5px;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
          .text {
            font-size: 12px;
            line-height: 16px;
            margin: 0;
            span.up,
            span.down {
              margin-right: 5px;
              display: inline-block;
              width: 15px;
              height: 15px;
              background-size: 15px 15px;
              vertical-align: middle;
            }
            span.up {
              background-image: url("../../assets/SVG/thumb_up.svg");
            }
            span.down {
              background-image: url("../../assets/SVG/thumb_down.svg");
            }
          }
        }
      }
    }
    .no-rating {
      padding: 16px 0;
      font-size: 12px;
      color: rgb(147, 153, 159);
    }
  }
}
</style>