<template>
  <div class="ratingselect" v-if="ratings">
    <div class="rating-type">
      <span class="block positive" @click="select(2)" :class="{'active':selectType==2}">
        全部
        <span class="count">{{ratings.length}}</span>
      </span>
      <span class="block positive" @click="select(0)" :class="{'active':selectType==0}">
        满意
        <span class="count">{{positives.length}}</span>
      </span>
      <span class="block negative" @click="select(1)" :class="{'active':selectType==1}">
        不满意
        <span class="count">{{negatives.length}}</span>
      </span>
    </div>
    <div class="switch" @click="$emit('toggle')" :class="{'on':onlyContent}">
      <span class="icon"></span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["ratings", "onlyContent", "selectType"],
  computed: {
    positives() {
      // 好评时rating.rateType=0
      return this.ratings.filter(rating => {
        return rating.rateType === 0;
      });
    },
    negatives() {
      // 差评时rating.rateType=1
      return this.ratings.filter(rating => {
        return rating.rateType === 1;
      });
    }
  },
  methods: {
    select(type) {
      this.$emit("select", type);
      // console.log("选择了这个类型");
    }
  }
};
</script>

<style lang="less">
.ratingselect {
  .rating-type {
    padding: 18px 0;
    margin: 0 18px;
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    .block {
      display: inline-block;
      padding: 8px 12px;
      margin-right: 10px;
      line-height: 16px;
      border-radius: 1px;
      font-size: 12px;
      color: rgb(77, 85, 93);
      &.positive {
        background: rgba(0, 160, 220, 0.2);
        &.active {
          background: rgb(0, 160, 220);
          color: #fff;
        }
      }
      &.negative {
        background: rgba(77, 85, 93, 0.2);
        &.active {
          background: rgb(77, 85, 93);
          color: #fff;
        }
      }
      .count {
        margin-left: 5px;
        font-size: 8px;
      }
    }
  }
  .switch {
    padding: 12px 18px;
    line-height: 24px;
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    color: rgb(147, 153, 159);
    &.on {
      color: #00c850;
    }
    .icon {
      display: inline-block;
      vertical-align: middle;
      margin-right: 4px;
      font-size: 24px;
      background-image: url("../../assets/SVG/check_circle.svg");
      background-size: 20px 20px;
      width: 20px;
      height: 20px;
    }
    .text {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
    }
  }
}
</style>