<template>
  <div class="star">
    <span
      class="star-item"
      v-for="(itemClass, index) in itemClasses"
      :key="index"
      :class="itemClass"
    ></span>
  </div>
</template>

<script>
const LENGTH = 5;
const IsON = "on";
const IsHALF = "half";
const IsOFF = "off";

export default {
  props: ["score"],
  computed: {
    itemClasses() {
      let result = [];
      let score = Math.floor(this.score * 2) / 2;
      let hasDecimal = score % 1 !== 0;
      let integer = Math.floor(score);
      // console.log(integer);

      for (let i = 0; i < integer; i++) {
        result.push(IsON);
      }
      if (hasDecimal) {
        result.push(IsHALF);
      }
      while (result.length < LENGTH) {
        result.push(IsOFF);
      }
      // console.log(result);

      return result;
    }
  }
};
</script>

<style lang="less" scope>
.star {
  display: inline-block;
  margin: 0 12px;
  vertical-align: top;
  .star-item {
    display: inline-block;
    background-repeat: no-repeat;
    width: 15px;
    height: 15px;
    margin-right: 5px;
    background-size: 15px 15px;
    &.on {
      background-image: url("../../assets/img/star36_on@2x.png");
    }
    &.off {
      background-image: url("../../assets/img/star36_off@2x.png");
    }
    &.half {
      background-image: url("../../assets/img/star36_half@2x.png");
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
</style>