<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <li
            v-for="(item, index) in goods"
            :key="index"
            class="menu-item"
            @click="selectMenu(index,$event)"
            ref="menuList"
            :class="{'current':currentIndex===index}"
          >
            <span class="text">
              <span v-show="item.type>0" class="icon"></span>
              {{item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
          <li v-for="(item, index) in goods" :key="index" class="food-list" ref="foodList">
            <!-- <div v-if="current==index"> -->
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li
                v-for="(food, index) in item.foods"
                :key="index"
                class="food-item"
                @click="selectFood(food)"
              >
                <div class="icon">
                  <img width="57" height="57" :src="food.icon" alt />
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span class="count">月售{{food.sellCount}}份</span>
                    <span>好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span>
                    <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol
                      :food="food"
                      :minPrice="seller.minPrice"
                      :deliveryPrice="seller.deliveryPrice"
                      @add="addFood"
                    ></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
            <!-- </div> -->
          </li>
        </ul>
      </div>
      <shopcard ref="shopcart" :Foods="Foods" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice"></shopcard>
    </div>
    <food ref="food" :selectedFood="selectedFood" @add="addFood"></food>
  </div>
</template>

<script>
// 添加better-scroll依赖
import BScroll from "better-scroll";
import food from "../../components/food/food";
import shopcard from "../../components/shopcard/shopcard";
import cartcontrol from "../../components/cartcontrol/cartcontrol";
// const ERR_OK = 0;
// const debug = process.env.NODE_ENV !== "production";
export default {
  props: ["seller"],
  data() {
    return {
      goods: [],
      // current: 0,
      scrollY: 0,
      listHeight: [],
      selectedFood: {}
    };
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        // console.log(this.scrollY,height1, height2);

        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          // 滚动食物列表，实现食物列表项与菜单列表项对应起来
          this.followScroll(i);
          return i;
        }
      }
      return 0;
    },
    Foods() {
      let foods = [];
      this.goods.forEach(good => {
        good.foods.forEach(food => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      console.log(foods);

      return foods;
    }
  },
  components: {
    food,
    shopcard,
    cartcontrol
  },
  methods: {
    getGoodsMsg() {
      this.axios
        .get("http://api.kele8.cn/agent/http://ustbhuangyi.com/sell/api/goods")
        .then(response => {
          ({ data: this.goods } = response.data);

          this.$nextTick(() => {
            this.initScroll();
            this.calculateHeight();
          });
        });
      // const url = debug
      //   ? "/api/goods"
      //   : "http://ustbhuangyi.com/sell/api/goods";
      // this.$http.get(url).then(response => {
      //   response = response.body;
      //   if (response.errno === ERR_OK) {
      //     this.goods = response.data;
      //     this.$nextTick(() => {
      //       this._initScroll();
      //       this._calculateHeight();
      //     });
      //   }
      // });
    },
    selectMenu(index) {
      let foodList = this.$refs.foodList;
      let el = foodList[index];
      // 滚动到指定的目标元素。
      this.foodsScroll.scrollToElement(el, 300);
      // this.current = index;
    },
    initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true
      });
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
        click: true,
        probeType: 3
      });
      // 判断滑动方向，避免下拉时分类高亮错误
      this.foodsScroll.on("scroll", pos => {
        if (pos.y <= 0) {
          this.scrollY = Math.abs(Math.round(pos.y));
        }
      });
    },
    calculateHeight() {
      // 获取所有的食物列表
      let foodList = this.$refs.foodList;
      let height = 0;
      this.listHeight.push(height);
      // 获取每个列表距离顶部的高度
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
      console.log(height, this.listHeight);
    },
    followScroll(index) {
      let menuList = this.$refs.menuList;
      let el = menuList[index];
      this.menuScroll.scrollToElement(el, 300, 0, -100);
    },
    selectFood(food) {
      this.selectedFood = food;
      console.log(this.$refs.food);

      this.$refs.food.show();
      console.log("点击了食物", this.selectedFood);
    },
    addFood(target) {
      console.log("添加小球", target);
      this._drop(target)
    },
    _drop(target) {
      // 体验优化,异步执行下落动画
      this.$nextTick(() => {
        this.$refs.shopcart.drop(target);
      });
    }
  },
  created() {
    this.getGoodsMsg();
  }
};
</script>

<style lang="less">
.goods {
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
  .menu-wrapper {
    flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;
    ul {
      padding: 0;
      margin: 0;
      .menu-item {
        display: table;
        height: 54px;
        width: 56px;
        padding: 0 12px;
        line-height: 14px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        &.current {
          position: relative;
          z-index: 10;
          margin-top: -1px;
          background: #fff;
          font-weight: 700;
        }
        .text {
          display: table-cell;
          width: 56px;
          vertical-align: middle;
          font-size: 12px;
          .icon {
            display: inline-block;
            vertical-align: top;
            width: 12px;
            height: 12px;
            margin-right: 2px;
            background-image: url("../../assets/img/decrease_3@2x.png");
            background-size: 12px 12px;
            background-repeat: no-repeat;
          }
        }
      }
    }
  }

  .foods-wrapper {
    flex: 1;
    ul {
      padding: 0;
      margin: 0;
      .title {
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left: 2px solid #d9dde1;
        font-size: 12px;
        color: rgb(147, 153, 159);
        background: #f3f5f7;
        margin: 0;
      }
      ul {
        padding: 0;
        margin: 0;
        .food-item {
          display: flex;
          margin: 18px;
          padding-bottom: 18px;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          position: relative;
          &:last-child {
            border: none;
          }
          .icon {
            flex: 0 0 57px;
            margin-right: 10px;
          }
          .content {
            flex: 1;
            .name {
              margin: 2px 0 8px 0;
              height: 14px;
              line-height: 14px;
              font-size: 14px;
              color: rgb(7, 17, 27);
            }
            .desc,
            .extra {
              line-height: 10px;
              font-size: 10px;
              color: rgb(147, 153, 159);
            }
            .desc {
              line-height: 12px;
              margin-bottom: 8px;
            }
            .extra {
              .count {
                margin-right: 12px;
              }
            }
            .price {
              font-weight: 700;
              line-height: 24px;
              .now {
                margin-right: 8px;
                font-size: 14px;
                color: rgb(240, 20, 20);
              }
              .old {
                text-decoration: line-through;
                font-size: 10px;
                color: rgb(147, 153, 159);
              }
            }
            .cartcontrol-wrapper {
              position: absolute;
              right: 0;
              bottom: 12px;
            }
          }
        }
      }
    }
  }
}
</style>